/* ================== THEME 5 | ONE-LINE LEFT-ALIGNED | PRODUCT TOOLS ================== */

#_pi_surveyWidgetContainer,
#_pi_surveyWidgetContainer.mobile-enabled {
	background-color: #fff !important;
	margin: 0px auto 30px !important;
	border: 0 !important;
}

#_pi_surveyWidgetContainer.mobile-enabled {
	margin: 0px auto 20px !important;
}

#_pi_surveyWidget {
	margin: 0px auto 0px !important;
	padding: 0px 0 0px !important;
    background-color: transparent !important;      
    border: 0px solid transparent !important;
}

#_pi_surveyWidgetContainer.mobile-enabled #_pi_surveyWidget {
	margin: 0px auto 0px !important;
	padding: 20px 0 0px !important;
    background-color: transparent !important;  
}


	/* 	CLOSE BUTTON */
	div._pi_closeButton {display: none !important; }


	/*  BRANDING */
	a._pi_branding { display: none !important; }


	/*  ITEMS PER ROW */
	div._pi_widgetContentContainer {
/* 		align-items: flex-start !important; */
		align-items: center !important;
	}

		div._pi_question {
			margin: 		0;
			padding: 		0 10px 0 0;
			flex: 			0 1 40%;

			font-size: 		18px;
			text-align: 	left !important;
/* 			background-color: lavender !important; */
		}
		
		ul._pi_answers_container {
			flex: 			0 1 60%;
/* 			background-color: pink !important; */
		}

/*
		@media (min-width:415px) and (max-width:768px) {
			div._pi_question {
				flex: 			0 1 50%;
			}
			
			ul._pi_answers_container {
				flex: 			0 1 50%;
			}
		}
*/



		#_pi_surveyWidgetContainer.mobile-enabled div._pi_question {
			margin: 		0;
			padding: 		0 0 10px 0 !important;
			flex: 			0 1 80%;

			text-align: 	center !important;
/* 			background-color: lavender !important; */
		}
		
		#_pi_surveyWidgetContainer.mobile-enabled ul._pi_answers_container {
			flex: 			0 1 auto;
/* 			background-color: pink !important; */
		}

			
			/* ====== LIST ITEM PADDINGS ======= */
			
			/* Single Choice */
			div[data-question-type="single_choice_question"] li a label {
				padding: 15px 15px !important;
			}

			div[data-question-type="single_choice_question"][data-radio-button="on"] li a label {
				padding: 15px 10px !important;
			}
					
			@media (min-width:415px) and (max-width:768px) {
				div[data-question-type="single_choice_question"] li a label {
					padding: 15px 10px !important;
				}

				div[data-question-type="single_choice_question"][data-radio-button="on"] li a label {
					padding: 15px 10px !important;
				}
			}
		
			#_pi_surveyWidgetContainer.mobile-enabled div[data-question-type="single_choice_question"] li a label {
				font-size: 11px !important;
				padding: 15px 15px !important;
			}

			#_pi_surveyWidgetContainer.mobile-enabled div[data-question-type="single_choice_question"][data-radio-button="on"] li a label {
				padding: 10px 8px !important;
			}
			
			
			
			/* Mutiple Choice */
			ul._pi_answers_container li label._pi-control-checkbox {
				padding: 15px 10px 15px 0 !important;
/* 				background-color: lightgreen !important; */
			}

			#_pi_surveyWidgetContainer.mobile-enabled label._pi-control._pi-control-checkbox {
				padding: 10px 10px 10px 0 !important;
			}
			